import React, { useState } from "react";

import { connect, useDispatch, useSelector } from "react-redux";
import { addContact } from "../redux/contacts/contacts-operations";
import {getContacts} from '../redux/selectors'

import s from "./ContactForm.module.css";
console.log(addContact);
function ContactForm({ phonebookContacts, onSubmit }) {
  const [name, setName] = useState("");
  const [number, setNumber] = useState("");
  const dispatch = useDispatch()

  const contacts = useSelector(getContacts)
  

 

  //onChangeInput
  const handleChange = (e) => {
    const { name, value } = e.target;

    switch (name) {
      case "name":
        setName(value);
        break;

      case "number":
        setNumber(value);
        break;

      default:
        return;
    }
  };
  //onSubmitForm
  const handleSubmit = (e) => {
    e.preventDefault();
 const cont = {
      name: name,
      phone: number,
    };
     if (contacts.some(({ name }) => name === cont.name)) {
      return alert(`Attention, the contact is already in contacts list`);
    }
    // dispatch(addContact(cont))
    dispatch(addContact({name, number}))
    

    setName("");
    setNumber("");
  };

  return (
    <div>
      <form onSubmit={handleSubmit}>
        <label>
          Name
          <input
            className={s.input__field}
            onChange={handleChange}
            type="text"
            name="name"
            value={name}
            pattern="^[a-zA-Z–∞-—è–ê-–Ø]+(([' -][a-zA-Z–∞-—è–ê-–Ø ])?[a-zA-Z–∞-—è–ê-–Ø]*)*$"
            title="–ò–º—è –º–æ–∂–µ—Ç —Å–æ—Å—Ç–æ—è—Ç—å —Ç–æ–ª—å–∫–æ –∏–∑ –±—É–∫–≤, –∞–ø–æ—Å—Ç—Ä–æ—Ñ–∞, —Ç–∏—Ä–µ –∏ –ø—Ä–æ–±–µ–ª–æ–≤. –ù–∞–ø—Ä–∏–º–µ—Ä Adrian, Jacob Mercer, Charles de Batz de Castelmore d'Artagnan –∏ —Ç. –ø."
            required
          />
        </label>
        <label>
          Number
          <input
            className={s.input__field}
            onChange={handleChange}
            type="tel"
            name="number"
            value={number}
            pattern="\+?\d{1,4}?[-.\s]?\(?\d{1,3}?\)?[-.\s]?\d{1,4}[-.\s]?\d{1,4}[-.\s]?\d{1,9}"
            title="–ù–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞ –¥–æ–ª–∂–µ–Ω —Å–æ—Å—Ç–æ—è—Ç—å —Ü–∏—Ñ—Ä –∏ –º–æ–∂–µ—Ç —Å–æ–¥–µ—Ä–∂–∞—Ç—å –ø—Ä–æ–±–µ–ª—ã, —Ç–∏—Ä–µ, –∫—Ä—É–≥–ª—ã–µ —Å–∫–æ–±–∫–∏ –∏ –º–æ–∂–µ—Ç –Ω–∞—á–∏–Ω–∞—Ç—å—Å—è —Å +"
            required
          />
        </label>
        <button className={s.form__button} type="submit">
          Add contact
        </button>
      </form>
    </div>
  );
}

// const mapStateToProps = ({ contacts: { phonebookContacts } }) =>
//   phonebookContacts;



const mapDispatchToProps = (dispatch) => ({
  onSubmit: (name, number) => dispatch(addContact(name, number)),
});

export default connect(null, mapDispatchToProps)(ContactForm);
